@import url('https://fonts.googleapis.com/css2?family=Joan&family=Londrina+Shadow&family=Lora:ital,wght@1,500&display=swap');

.main{
    text-align:center;
    justify-content:center;
    display:flex;
    padding: 90px 0 90px 0;
    background: linear-gradient(#0377b1,#03bd7f);}

.sub-main{
    display:flex;
    justify-content: center;
    height: 600px;
    width:30%;
    box-shadow: 11px 12px 13px 14px rgba(207,207,207,0.75);
    padding-top: 30px;
    border-radius: 16px;
    background: white;

}

.imgs{
    padding-top: 20px;
    justify-content: center;
    display:flex;

}
h1{
    font-family: 'Joan', serif;
    font-size: 50px;
}

.container-image{
  background-color: rgb(223,221,221);  
  border-radius: 150px;
  align-items: center;
  display:flex;
  height:155px;
  width:155px;
}


.profile{
    height: 130px;
    width: 140px;
    border-radius: 130px; 
}

input{
    width: 300px;
    height: 50px;
    border-radius: 60px;
    box-shadow:inset 0px 0px 15px 0px #89dfc2;
    border:3px solid #89dfc2;
    outline: none;
    background-color: white;
}
.checkbox{
width: 16px;
height:16px;
}

.checklabel{
    display: flex;
    align-items:flex-start;
    padding-top:8px;
    font-size: 18px;
}

.email{
    height: 25px;
    width:25px;
    position:absolute;
    padding: 14px 0 0 25px;
}

.pass{
    height: 25px;
    width:25px;
    position:absolute;
    padding: 14px 0 0 25px;
}

.name{
    padding-left: 70px;
    font-size: 20px;

}

.second-input{
    padding-top:13px;
}

button{
    width: 300px;
    height: 50px;
    border-radius: 60px;
    background-color:#03bd7f;
    border: none;
    color: white;
    font-size: 25px;
    cursor: pointer;
}

.login-button{
    padding-top: 15px;

}

a{
    font-family: 'Joan', serif;

}